import Vue from "vue";

export const EventBus = new Vue();

// propertyOrder 
export function orderProperty(obj) {
    // 
    // 
    const keys = Object.keys(obj);
    // 
    //if(keys.length <= 1) return obj;
    return keys.map(key => {
        return {
            key,
            val: obj[key]
        };
    }).sort((pre, cur) => {
        return (pre.val.propertyOrder || 999) - (cur.val.propertyOrder || 999);
    });
}

